@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

*{
    margin: 0;
    padding: 0;
}

body{
    background: #fff;
    color: #222;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
}

header{
    position: relative;
}

.wrapper{
    max-width: 1280px;
    margin: 50px auto;
    padding: 0 10px;
}

@media (max-width:1280px){
    .wrapper{
        max-width: 970px;
    }
    main .item{
        width: 50%;
    }
}
@media (max-width:992px){
    .wrapper{
        max-width: 750px;
    }
}
@media (max-width:768px){
    .wrapper{
        max-width: none;
    }
}

header .logo{
    font-weight: 600;
    font-size: 20px;
}

header ul.nav{
    float: right;
    margin-top: 3px;
}

header ul.nav li{
    display: inline;
    margin-left: 25px;
    cursor: pointer;
    transition: opacity 400ms ease;
}

header ul.nav li:hover{
    opacity: 0.6;
}

.shop-cart-open{
    color: #f5f5f5;
    position: fixed;
    bottom: 10%;
    right: 5%;
    float: right;
    cursor: pointer;
    background: #ca5252;
    height: 50px;
    width: 50px;
    line-height: 10px;
    border-radius: 50%;
    overflow: visible;
    transition: all 500ms ease;
    z-index: 1002;
}

.shop-cart-close{
    color: #f5f5f5;
    position: fixed;
    bottom: 10%;
    right: 5%;
    float: right;
    cursor: pointer;
    background: #ca5252;
    height: 50px;
    width: 50px;
    line-height: 10px;
    border-radius: 50%;
    overflow: visible;
    transition: all 500ms ease;
    z-index: 1001;
}

.shop-cart-close.active{
    right: 35%;
}

.shop-cart-open.active{
    opacity: 0%;
    right: 35%;
}

.shop-cart.active{
    position: fixed;
    right: 0;
}

.shop-cart{
    transition: all 500ms ease;
    position: absolute;
    position: fixed;
    top:0%;
    right: -50%;
    width: 35%;
    background: #f5f5f5;
    -webkit-box-shadow: -14px -2px 50px 2px rgba(0,0,0,0.71);
    box-shadow: -14px -2px 50px 2px rgba(0,0,0,0.71);
    z-index: 1000;
}

.cart-empty{
    text-align: center;
    padding: 20px;
}

 .item{
    padding: 20px;
    padding-bottom: 0;
    width: 100%;
    float: left;
    margin-bottom: 10px;
}

.item *{
    position: relative;
}

.item {
    transition: all 500ms ease;
}

.item.active{
    background-color:#f54c4c;
}

.item img{
    width: 70px;
    float: left;
}

.item p{
    font-weight: 600;
}

.item .cart-delete{
    color: #ca5252;
    font-size: 50px;
    float: right;
    position: relative;
    top: -25px;
    right: 25px;
    transition: all 300ms ease;
}

.item .cart-delete:hover{
    transform: scale(1.5);
}

.cart-empty .number-cart{
    color: #ca5252;
}

@media (max-width:768px){
    header ul.nav li{
        margin-left: 10px;
    }
}

header .presentation{
    margin: 50px 0;
    background: url('./img/back.jpg') no-repeat;
    width: 100%;
    height: 450px;
    background-size: cover;
    background-position: center center;
    background-blend-mode: multiply;
    background-color: #9e9e9e;
    position: relative;
}

@media (max-width:768px){
    header .presentation{
        height: 350px;
    }
}

header .presentation:after{
    content: 'Очки с недочетами по скидке';
    position: absolute;
    top: 100px;
    left: 50px;
    font-size: 40px;
    font-weight: 600;
    width: 300px;
    color: #fff;
}

header .presentation:before{
    content: 'от -40% до -80%';
    position: absolute;
    top: 240px;
    left: 50px;
    font-size: 15px;
    font-weight: 600;
    width: 300px;
    color: #fff;
}

main{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
}

main .item{
    width: 30%;
    margin-bottom: 50px;
    background: #f5f5f5;
    overflow: hidden;
    position: relative;
    padding-bottom: 20px;
    border-radius: 10px 10px 0 0;
}

@media (max-width:1280px){
    main .item{
        width: 45%;
    }
}
@media (max-width:992px){
    main .item{
        width: 44%;
    }
}
@media (max-width:768px){
    main .item{
        width: 42%;
    }
}
@media (max-width:592px){
    main .item{
        width: 40%;
    }
}
@media (max-width:472px){
    main .item{
        width: 44%;
        padding: 2%;
    }
    main h2, main p, main b {
        margin: 0;
        color: #333;
    }
}
main .item img{
    width: 100%;
    border-radius: 10px 10px 0 0;
    transition: transform 500ms ease;
}

main .item img:hover{
    transform: scale(1.05);
}

main h2, main p, main b {
    margin: 10px 20px;
    color: #333;
}

@media (max-width:472px){
    main h2, main p, main b {
        margin: 4px 10px;
        color: #333;
    }
}

main .add-to-cart{
    position: absolute;
    right: 20px;
    bottom: 20px;
    background: #ca5252;
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    color: #fff;
    border-radius: 50%;
    cursor: pointer;
    font-weight: 600;
    transition: transform 300ms ease;
}

@media (max-width:768px){
    main .add-to-cart{
        right: 0px;
        bottom: 0px;

        margin: 10px 10px;
        vertical-align:bottom;
    }
}

main .add-to-cart:hover{
    transform: scale(1.5);
}

footer{
    text-align: center;
    margin-top: 50px;
}